.banner {
    position: relative;
    width: 70%;
    height: 500px;
    background: #f2f2f2;
    margin-bottom: 80px;
    right: -15%;
}

.right {
    position: absolute;
    transform: translateY(-50%);
    top: 50%;
    right: 5%;
    background: transparent;
}

.right img {
    background: transparent;
}

.left {
    position: absolute;
    top: 50%;
    left: 5%;
    transform: translateY(-50%);
    background: transparent;
}

.left img {
    background: transparent;
}